/**
 * utils ‰∏∫Â∑•ÂÖ∑ÂáΩÊï∞ÔºåÂØπÂéüÁîüAPIÂÅöÂÖºÂÆπÊÄßÂ§ÑÁêÜÂèäÊèêÂèñÂÖ¨ÂÖ±ÊñπÊ≥ï
 */
const utils = {
  // Èº†Ê†áÊªöËΩÆ‰∫ã‰ª∂
  getWheelDelta(event) {
    if (event.wheelDelta) {
      // Á¨¨‰∏ÄÊ¨°Ë∞ÉÁî®‰πãÂêéÊÉ∞ÊÄßËΩΩÂÖ•
      this.getWheelDelta = (e) => e.wheelDelta;

      // Á¨¨‰∏ÄÊ¨°Ë∞ÉÁî®‰ΩøÁî®
      return event.wheelDelta;
    }
    // ÂÖºÂÆπÁÅ´Áãê
    this.getWheelDelta = (e) => -e.detail;
    return -event.detail;
  },
  // Èò≤ÊäñÂä®ÂáΩÊï∞Ôºåmethod ÂõûË∞ÉÂáΩÊï∞Ôºåcontext ‰∏ä‰∏ãÊñáÔºåevent ‰º†ÂÖ•ÁöÑÊó∂Èó¥Ôºådelay Âª∂ËøüÂáΩÊï∞
  // Ë∞ÉÁî®ÁöÑÊó∂ÂÄôÁõ¥Êé•ÊâßË°åÔºåÊ≥®ÊÑèÂíå throttle Âú®‰ΩøÁî®ÁöÑÊó∂ÂÄôÁöÑÂå∫Âà´
  debounce(method, context, event, delay) {
    clearTimeout(method.tId);
    method.tId = setTimeout(() => {
      method.call(context, event);
    }, delay);
  },
  // Êà™ÊµÅÂáΩÊï∞Ôºåmethod ÂõûË∞ÉÂáΩÊï∞Ôºåcontext ‰∏ä‰∏ãÊñáÔºådelay Âª∂ËøüÂáΩÊï∞Ôºå
  // ËøîÂõûÁöÑÊòØ‰∏Ä‰∏™ÂáΩÊï∞
  throttle(method, context, delay) {
    let wait = false;
    return function (...args) {
      if (!wait) {
        method.apply(context, args);
        wait = true;
        setTimeout(() => {
          wait = false;
        }, delay);
      }
    };
  },
  // Âà†Èô§ Á±ªÂêç
  deleteClassName(el, className) {
    if (el.classList.contains(className)) {
      el.classList.remove(className);
    }
  },
  // polyfill Object.assign
  polyfill() {
    if (typeof Object.assign !== 'function') {
      Object.defineProperty(Object, 'assign', {
        value: function assign(target) {
          if (target == null) {
            throw new TypeError('Cannot convert undefined or null to object');
          }
          const to = Object(target);
          for (let index = 1; index < arguments.length; index += 1) {
            const nextSource = arguments[index];
            if (nextSource != null) {
              for (const nextKey in nextSource) {
                if (Object.prototype.hasOwnProperty.call(nextSource, nextKey)) {
                  to[nextKey] = nextSource[nextKey];
                }
              }
            }
          }
          return to;
        },
        writable: true,
        configurable: true,
      });
    }
  },
};

export default utils;
