import utils from './utils';

/**
 * –ü–æ–ª–Ω–æ—ç–∫—Ä–∞–Ω–Ω–∞—è –ª–æ–≥–∏–∫–∞ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏
 */
class PureFullPage {
  constructor(options) {
    // —Ä–∞—Å–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
    const defaultOptions = {
      isShowNav: true,
      delay: 1000,
      // definePages: () => { },
    };
    utils.polyfill();
    // –æ–±—ä–µ–¥–∏–Ω–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫—É—é –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—é
    // this.options = Object.assign(defaultOptions, options);
    this.options = options || defaultOptions;

    // –ü—Ä–∏–≤—è–∑—ã–≤–∞–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫—É—é —Ñ—É–Ω–∫—Ü–∏—é –∫ —ç–∫–∑–µ–º–ø–ª—è—Ä—É this
    // this.options.definePages = this.options.definePages.bind(this);
    // –ü–æ–ª—É—á–∏—Ç—å –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –ø–µ—Ä–µ–ª–∏—Å—Ç—ã–≤–∞–Ω–∏—è —Å—Ç—Ä–∞–Ω–∏—Ü
    this.container = document.querySelector('.main-content');
    // –ü–æ–ª—É—á–∞–µ–º –æ–±—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç—Ä–∞–Ω–∏—Ü, –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–Ω—ã—Ö –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –ø—Ä–∞–≤–æ–π —Ç–æ—á–∫–∏ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
    this.pages = document.querySelectorAll('.section');
    this.pagesNum = this.pages.length;
    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º –Ω–∞–≤–∏–≥–∞—Ü–∏—é —Å –ø—Ä–∞–≤–æ–π —Å—Ç–æ—Ä–æ–Ω—ã –¥–ª—è –¥–∞–ª—å–Ω–µ–π—à–µ–≥–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è
    this.navDots = [];
    // –ü–æ–ª—É—á–∏—Ç—å —Ç–µ–∫—É—â—É—é –≤—ã—Å–æ—Ç—É –ø—Ä–æ—Å–º–æ—Ç—Ä–∞
    this.viewHeight = document.documentElement.clientHeight;
    // –¢–µ–∫—É—â–∞—è –ø–æ–∑–∏—Ü–∏—è, –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è–µ—Ç —Å–æ–±–æ–π
    // —Å–º–µ—â–µ–Ω–∏–µ –≤–Ω–∏–∑ –æ—Ç –≤–µ—Ä—Ö–∞ –æ–∫–Ω–∞ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞
    this.currentPosition = 0;
    // –≤—Ä–µ–º—è –∑–∞–¥–µ—Ä–∂–∫–∏ —Ñ—É–Ω–∫—Ü–∏–∏ –ø–µ—Ä–µ—Ö–≤–∞—Ç–∞ / –∏–Ω—Ç–µ—Ä–≤–∞–ª–∞, –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥—ã
    this.DELAY = this.options.delay;
    // –ß—Ç–æ–±—ã –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Å–∫–æ–ª—å–∂–µ–Ω–∏—è, –Ω—É–∂–Ω–æ –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å —Ç–æ–ª—å–∫–æ –æ—Ä–¥–∏–Ω–∞—Ç—É
    this.startY = undefined;

    /**
     * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è
     */
    this.init();
  }
  // –ü–æ–≤—Ç–æ—Ä–Ω–æ–µ –ø–æ–ª—É—á–µ–Ω–∏–µ –ø–æ–∑–∏—Ü–∏–∏ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–∞–∑–º–µ—Ä–∞ –æ–∫–Ω–∞

  getNewPosition() {
    this.viewHeight = document.documentElement.clientHeight;
    this.container.style.height = `${this.viewHeight}px`;
    let activeNavIndex;
    this.navDots.forEach((e, i) => {
      if (e.classList.contains('active')) {
        activeNavIndex = i;
      }
    });
    this.currentPosition = -(activeNavIndex * this.viewHeight);
    this.turnPage(this.currentPosition);
  }

  handleWindowResize(event) {
    // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é –∑–∞—â–∏—Ç—ã –æ—Ç —Å–æ—Ç—Ä—è—Å–µ–Ω–∏—è
    utils.debounce(this.getNewPosition, this, event, this.DELAY);
  }
  // –ø–µ—Ä–µ—Ö–æ–¥ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É

  turnPage(height) {
    this.container.style.top = `${height}px`;
  }
  // –ò–∑–º–µ–Ω—è–µ–º —Å—Ç–∏–ª—å –ø—Ä–∏ –ø—Ä–æ–∫—Ä—É—Ç–∫–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã

  changeNavStyle(height) {
    if (this.options.isShowNav) {
      this.navDots.forEach(el => {
        utils.deleteClassName(el, 'active');
      });

      const i = -(height / this.viewHeight);
      this.navDots[i].classList.add('active');
    }
  }
  // –°–æ–∑–¥–∞–Ω–∏–µ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ –ø–æ –ø—Ä–∞–≤–æ–π —Å—Ç–æ—Ä–æ–Ω–µ

  createNav() {
    const nav = document.createElement('div');
    nav.className = 'nav';
    this.container.appendChild(nav);

    // –ï—Å—Ç—å –Ω–µ—Å–∫–æ–ª—å–∫–æ —Å—Ç—Ä–∞–Ω–∏—Ü, –ø–æ–∫–∞–∑—ã–≤–∞—é—â–∏—Ö –Ω–µ—Å–∫–æ–ª—å–∫–æ —Ç–æ—á–µ–∫
    for (let i = 0; i < this.pagesNum; i += 1) {
      nav.innerHTML += '<p class="nav-dot"><span></span></p>';
    }

    const navDots = document.querySelectorAll('.nav-dot');
    this.navDots = Array.prototype.slice.call(navDots);

    // –î–æ–±–∞–≤–ª—è–µ–º –Ω–∞—á–∞–ª—å–Ω—ã–π —Å—Ç–∏–ª—å
    this.navDots[0].classList.add('active');

    // –î–æ–±–∞–≤–∏—Ç—å —Å–æ–±—ã—Ç–∏–µ click –ø–æ —Ç–æ—á–∫–µ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
    this.navDots.forEach((el, i) => {
      el.addEventListener('click', () => {
        // –ø–µ—Ä–µ—Ö–æ–¥ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É
        this.currentPosition = -(i * this.viewHeight);
        // –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏
        // this.options.definePages();
        this.turnPage(this.currentPosition);

        // –∏–∑–º–µ–Ω–∏—Ç—å —Å—Ç–∏–ª—å
        this.navDots.forEach((el) => {
          utils.deleteClassName(el, 'active');
        });
        el.classList.add('active');
      });
    });
  }

  goUp() {
    // –°—Ç—Ä–∞–Ω–∏—Ü–∞ –ø—Ä–æ–∫—Ä—É—á–∏–≤–∞–µ—Ç—Å—è –≤–≤–µ—Ä—Ö —Ç–æ–ª—å–∫–æ —Ç–æ–≥–¥–∞, –∫–æ–≥–¥–∞ –µ—Å—Ç—å —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤–≤–µ—Ä—Ö—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã
    if (-this.container.offsetTop >= this.viewHeight) {
      // –ü–æ–≤—Ç–æ—Ä–Ω–æ —É–∫–∞–∂–∏—Ç–µ currentPosition —Ç–µ–∫—É—â–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏–∑ –≤–µ—Ä—Ö–Ω–µ–π —á–∞—Å—Ç–∏ –ø—Ä–µ–¥—Å—Ç–∞–≤–ª–µ–Ω–∏—è,
      // —á—Ç–æ–±—ã –æ–±–µ—Å–ø–µ—á–∏—Ç—å –ø–æ–ª–Ω–æ—ç–∫—Ä–∞–Ω–Ω—É—é –ø—Ä–æ–∫—Ä—É—Ç–∫—É.CurrentPosition - –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ,
      // –∏ —á–µ–º –±–æ–ª—å—à–µ –∑–Ω–∞—á–µ–Ω–∏–µ, —Ç–µ–º –º–µ–Ω—å—à–µ —á–∞—Å—Ç—å –∑–∞ –≤–µ—Ä—Ö–Ω–∏–º
      this.currentPosition += this.viewHeight;

      this.turnPage(this.currentPosition);
      this.changeNavStyle(this.currentPosition);
      // –û–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏
      // this.options.definePages();
    }
  }

  goDown() {
    // –°—Ç—Ä–∞–Ω–∏—Ü–∞ –ø—Ä–æ–∫—Ä—É—á–∏–≤–∞–µ—Ç—Å—è –≤–Ω–∏–∑ —Ç–æ–ª—å–∫–æ —Ç–æ–≥–¥–∞, –∫–æ–≥–¥–∞ –µ—Å—Ç—å —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤–Ω–∏–∑—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã
    if (-this.container.offsetTop <= this.viewHeight * (this.pagesNum - 2)) {
      // –ü–æ–≤—Ç–æ—Ä–Ω–æ —É–∫–∞–∂–∏—Ç–µ currentPosition —Ç–µ–∫—É—â–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏–∑ –≤–µ—Ä—Ö–Ω–µ–π —á–∞—Å—Ç–∏ –ø—Ä–µ–¥—Å—Ç–∞–≤–ª–µ–Ω–∏—è,
      // —á—Ç–æ–±—ã –æ–±–µ—Å–ø–µ—á–∏—Ç—å –ø–æ–ª–Ω–æ—ç–∫—Ä–∞–Ω–Ω—É—é –ø—Ä–æ–∫—Ä—É—Ç–∫—É.CurrentPosition - –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ,
      // –∏ —á–µ–º –º–µ–Ω—å—à–µ –∑–Ω–∞—á–µ–Ω–∏–µ, —Ç–µ–º –±–æ–ª—å—à–µ —á–∞—Å—Ç—å –∑–∞ –≤–µ—Ä—Ö–Ω–∏–º
      this.currentPosition -= this.viewHeight;

      this.turnPage(this.currentPosition);
      this.changeNavStyle(this.currentPosition);

      // –û–±—Ä–∞–±–æ—Ç–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–∏—Ö —Ñ—É–Ω–∫—Ü–∏–π
      // this.options.definePages();
    }
  }
  // –õ–æ–≥–∏–∫–∞ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ –º—ã—à–∏ (–ª–æ–≥–∏–∫–∞ –∫–ª–∞–≤–∏—à –¥–ª—è –ø–æ–ª–Ω–æ—ç–∫—Ä–∞–Ω–Ω–æ–π –ø—Ä–æ–∫—Ä—É—Ç–∫–∏)

  scrollMouse(event) {
    const delta = utils.getWheelDelta(event);
    // –¥–µ–ª—å—Ç–∞ <0, –ø—Ä–æ–∫—Ä—É—Ç–∫–∞ –º—ã—à–∏ –≤–ø–µ—Ä–µ–¥, –ø—Ä–æ–∫—Ä—É—Ç–∫–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤–Ω–∏–∑
    if (delta < 0) {
      this.goDown();
    } else {
      this.goUp();
    }
  }
  // —Å–æ–±—ã—Ç–∏–µ —Å–µ–Ω—Å–æ—Ä–Ω–æ–≥–æ —ç–∫—Ä–∞–Ω–∞

  touchEnd(event) {
    const endY = event.changedTouches[0].pageY;
    if (endY - this.startY < 0) {
      // –ü—Ä–æ–≤–µ–¥–∏—Ç–µ –ø–∞–ª—å—Ü–µ–º –≤–≤–µ—Ä—Ö, –ø—Ä–æ–∫—Ä—É—Ç–∏—Ç–µ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É –≤–Ω–∏–∑
      this.goDown();
    } else {
      // –ü—Ä–æ–≤–µ–¥–∏—Ç–µ –ø–∞–ª—å—Ü–µ–º –≤–Ω–∏–∑, –ø—Ä–æ–∫—Ä—É—Ç–∏—Ç–µ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É –≤–≤–µ—Ä—Ö
      this.goUp();
    }
  }
  // –§—É–Ω–∫—Ü–∏—è –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏

  init() {
    this.container.style.height = `${this.viewHeight}px`;
    // –°–æ–∑–¥–∞–Ω–∏–µ —Ç–æ—á–µ—á–Ω–æ–π –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
    if (this.options.isShowNav) {
      this.createNav();
    }
    // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é –ø–µ—Ä–µ—Ö–≤–∞—Ç–∞
    const handleMouseWheel = utils.throttle(this.scrollMouse, this, this.DELAY);

    // –ú–æ–Ω–∏—Ç–æ—Ä–∏–Ω–≥ –∫–æ–ª–µ—Å–∏–∫–∞ –º—ã—à–∏, —Å–æ–±—ã—Ç–∏—è –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ –º—ã—à–∏ Firefox –æ—Ç–ª–∏—á–∞—é—Ç—Å—è –æ—Ç –¥—Ä—É–≥–∏—Ö
    if (navigator.userAgent.toLowerCase().indexOf('firefox') === -1) {
      document.addEventListener('mousewheel', handleMouseWheel);
    } else {
      document.addEventListener('DOMMouseScroll', handleMouseWheel);
    }

    // —Å–µ–Ω—Å–æ—Ä–Ω—ã–π —ç–∫—Ä–∞–Ω –º–æ–±–∏–ª—å–Ω–æ–≥–æ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
    document.addEventListener('touchstart', event => {
      this.startY = event.touches[0].pageY;
    });
    const handleTouchEnd = utils.throttle(this.touchEnd, this, 500);
    document.addEventListener('touchend', handleTouchEnd);
    document.addEventListener('touchmove', event => {
      event.preventDefault();
    });

    // –°–±—Ä–∞—Å—ã–≤–∞–µ–º –ø–æ–∑–∏—Ü–∏—é –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–∞–∑–º–µ—Ä–∞ –æ–∫–Ω–∞
    window.addEventListener('resize', this.handleWindowResize.bind(this));
  }
}

const fullpage = new PureFullPage({
  delay: 1000,
});
